class ChatDisplay(
    val chat: ChatBody
) {
    private val timeHash  = "at${Date().getTime().toString().slice(11..13)}" + elCount.toString()
    private val className = "chatDisplay $timeHash"
    private val selector  = ".chatDisplay.$timeHash"

    init {
        refresh()
        elCount ++
    }

    fun refresh() {
        remove()
        document.querySelector(".main .chatDisplay")?.append { div(className) }
        document.querySelector(selector)!!.append {


            table {
                for (msg in chat.messages)
                    tr {

                        // to style each role
                        val cls = msg.role.lowercase()

                        td(cls) { unsafe { +msg.content.replace("\n", "<br>") }}
                    }
            }
        }
    }

    fun remove() =
        document.querySelector(selector)?.remove()


    companion object {
        var elCount :Int = 0;
    }

}