@file:OptIn(DelicateCoroutinesApi::class)

import classes.data.Book
import classes.data.Chapter
import classes.misc.getListItemsFromOL
import classes.misc.network.models.ChatBody
import classes.misc.network.models.chatBody
import classes.ui.ChatDisplay
import kotlinx.browser.document
import kotlinx.browser.window
import kotlinx.html.dom.append
import org.w3c.dom.Node
import kotlinx.coroutines.*
import kotlinx.html.*
import kotlinx.html.dom.prepend
import kotlinx.html.js.onClickFunction
import org.w3c.dom.url.URLSearchParams


fun main() { GlobalScope.launch {


    //if no title
    if( ! window.location.href.contains("?title=")){
        document.querySelector(".main")?.append {
            h3 {
                +"An engaging book title: "
                br();
                input {
                    id = "txtBookTitle"
                    style = "width: 18em; font-size: 1.19em; font-weight: bold;"

                }
                br()
                div { style="font-size: .6em; font-weight: normal; margin-left: 5em;";
                   +"for example:"
                    br();
                    i {  +"'IRS Free File: a guide to doing your own taxes.'" }
                    br();
                    //a(href = "https://github.com/dair-ai/Prompt-Engineering-Guide/blob/main/guides/prompts-intro.md#general-tips-for-designing-prompts") { +"(prompt engineering tips)" }

                }
            }
            br()

            br()
            h4 {
                +"Book meta-description / instructions:"
                br()
                input {
                    id = "txtBookDesc"
                    style = "width: 30em; font-size: 1.05em;"
                }
                br()
                div { style="font-size: .6em; font-weight: normal; margin-left: 5em;";
                    +"for example:"
                    br();
                    i { +"'Assume I have income in both 1044 and 1099. Also deductions for my home office, computer parts, etc., as well as some charitable deductions. Dedicate chapter(s) for preparation, and (importantly) one for each IRS form.'" }

                }



            }
            br()
            button {
                +"Write"
                onClickFunction = {
                    window.location.href =
                        "${window.location.origin + window.location.pathname}" +
                        "?title=${document.querySelector("#txtBookTitle")?.asDynamic().value.toString()}" +
                        "&desc=${document.querySelector("#txtBookDesc")?.asDynamic().value.toString()}"
                        "";
                }
            }
            br()
            hr()
            br()
        }
        return@launch
    }

    var tit   =  js("""decodeURIComponent(new URLSearchParams(window.location.search).get('title'))""")
    var desc  =  js("""decodeURIComponent(new URLSearchParams(window.location.search).get('desc'))""")
    val book = Book(tit, desc)

    document.querySelector(".main")?.prepend { h2 { +"Book details" } }





    delay(1200)


    val chat = chatBody(0.8, 0.8, 0.45) {
        system ("Today we will be writing a book, titled:\n> ${book.title}" )
        system ("Book description / instructions:\n${book.desc}")
        user ("Write a brief descition / blob about the book, in 50 words or less." )
    }

    val chatDisplay = ChatDisplay(chat)

    book.blob = chat.sendRequest()
    chatDisplay.refresh()

    chat.apply {
        temperature      = 0.65;
        presencePenalty  = 0.5;
        frequencyPenalty = 0.7;
        user ("Generate a NUMBERED LIST with 7(minimum count) to 15(maximum count) chapter titles for this book.\nMake the first chapter an introduction, and the last chapter a conclusion.\nThe chapters should usually build on previous chapters, follow some logical progression, or have some thought to organization.\n\nThese chapter titles will eventually (after text expansion) be used to create the entire contents for the chapters.")
    }
    chatDisplay.refresh()

    chat.sendRequest()
    book.chapters = let{
        val lst = chat.messages.last().content.getListItemsFromOL()
        val lstOut = mutableListOf<Chapter>()
        lst.forEachIndexed { itIndex, it ->
            lstOut.add(Chapter(it, itIndex+1))
        }
        lstOut
    }
    chatDisplay.refresh()






    //======
    // part 2

    book.chapters.forEachIndexed { i,chapt ->

        document.querySelector(".main .chatDisplay")?.append { br(); h3 { +"Chapter ${i+1}"; br(); +chapt.name; } }
        var chapterChat = chatBody(0.7, .55, 0.45) {
            system ("Today we will be writing a chapter of a book titled: \"${book.title}\".\n > ${book.blob}")
            user   ("General writing instructions:\n${book.desc}")
            system ("There are ${book.chapters.size} chapters in this book:\n${book.chaptersStr}")
            system ( "HOWEVER - right now, we are ONLY writing chapter ${i+1}:\n > ${chapt.name}\n" )
            user   ( "Write a brief description/blob about the chapter in 100 words or less. This will be used in another message for prompt expansion.\n\nBegin your response with: \"In this Chapter, \"" )
        }
        var chatDisplay2 = ChatDisplay(chapterChat)
        chatDisplay2.refresh();
        chapt.blob = chapterChat.sendRequest()
        chatDisplay2.refresh()

        
        delay(1200)

        chapterChat = chatBody(1.0, .75, 0.35) {
            system ("Today we will be writing a chapter of a book titled: \"${book.title}\".\n > ${book.blob}")
            system ("There are ${book.chapters.size} chapters in this book:\n${book.chaptersStr}")
            system ( "However, right now, we are *ONLY* writing chapter #${i+1}:\n > ${chapt.name}!\n\n${chapt.blob}" )
            user ( "Write a paragraph (40 words or less) about the chapter:\n" +
                    "> An interesting / entertaining / humourous / wacky / engaging fact, or a point of context, expansion information, theory, aside, tangent, etc. to keep the chapter engaging. Maybe even a joke, a common misconception, or a made up personal anecdote. \n\n" +
                    "Below it, write \"Important Points:\", and a list with 2-6 items (<15 words each):\n" +
                    "> Most important / interesting points" )
        }
        chatDisplay2.remove()
        chatDisplay2 = ChatDisplay(chapterChat)

        chapt.blob += "\n"
        chapt.blob += chapterChat.sendRequest()

        chatDisplay2.refresh()

        delay(1200)

        chapterChat = chatBody(0.3, .5, 0.35) {
            system ("Today we will be writing a chapter of a book titled: \"${book.title}\".\n > ${book.blob}")
            system ("There are ${book.chapters.size} chapters in this book:\n${book.chaptersStr}")
            system ( "However, right now, we are *ONLY* writing chapter #${i+1}: ${chapt.name}!\n\n${chapt.blob}" )
            user   ( "Write the entire text content for this chapter.\n\nChapters are usually 400-1500 pages. Introduction and conclusion chapters are shorter than the main chapters.\nOrganize the chapter thoughtfully, and keep it interesting and engaging.\nYou don't have to use all of the information given - it was obtained via prompt expansion and may be arbitrary.\n" )
        }
        chatDisplay2.remove()
        chatDisplay2 = ChatDisplay(chapterChat)

        delay(1200)

        chapterChat.sendRequest()
        chatDisplay2.refresh()

        delay(1200)

    }



    document.body?.append {
        br();br();br();
        hr();
        br();
        h5 { +"assembling book..." }
        br();br();
        val bookcontents = book.createBookHTML();
        h2 {
            a(href = bookcontents) { +"Read your book here! " }
        }
        br();br();

    }


}}

fun Node.sayHello() {
    append {
        div {
            +"Hello from JS"
        }
    }
}