package classes.data

import classes.misc.network.models.ChatBody
import org.w3c.dom.url.URL
import org.w3c.files.Blob
import org.w3c.files.BlobPropertyBag

class Book (
    val title :String,
    val desc  :String
){
    var blob :String? = null
    var chapters :List<Chapter> = listOf()
    val chaptersStr :String get() {
        var a = ""
        chapters.forEachIndexed { i, chapt ->
            a += "${i+1}. ${chapt.name}\n"
        }
        return a
    }

    fun createBookHTML() :String {
        val htmlString = "<h1>$title </h1><br/><hr/><br/><br/>";

        val chapterContentsHTML = mutableListOf<String>(htmlString)
        chapters.forEach { chit -> chapterContentsHTML.add(chit.contentAsHTML()) }
        val htmlblob =  Blob( chapterContentsHTML.toTypedArray(), BlobPropertyBag("text/html"));
        val url = URL.createObjectURL(htmlblob);

        return url;

    }

}

class Chapter(
    val name :String,
    val chapterNum :Int,
    var blob :String? = null,
    var content :String? = null
){
    fun contentAsHTML() =
        """
            
            <h2><u>Chapter $chapterNum <br /></u> <small><i>$name</i></small></h2>
            <br>
            ${content}
            <br><br>
            
        """.trimIndent()

}

