package classes.misc.network
import kotlinx.html.*
import kotlinx.serialization.*
import kotlinx.serialization.json.*
import org.w3c.xhr.XMLHttpRequest
import kotlinx.browser.window
import kotlinx.coroutines.await
import kotlinx.html.*
import kotlinx.html.dom.create
import kotlinx.serialization.json.Json
import org.w3c.fetch.RequestInit
import kotlin.coroutines.resume
import kotlin.coroutines.suspendCoroutine
import kotlinx.browser.window
import kotlinx.coroutines.await
import kotlinx.coroutines.delay
import org.w3c.fetch.Headers
import org.w3c.fetch.Response
import kotlin.coroutines.resumeWithException


object Fetcher {

    val key :String get()= window.atob("c2stWXpWa2xiOTVGdHYwVW54ZDNUbDZUM0JsYmtGSjRYT2Zibk1vVHEzOFFFc0VQV0tJ")

    private fun newHeaders() = Headers().apply {
        append("Content-Type", "application/json")
        append("Authorization", "Bearer ${key}")
    }

    suspend fun post(url: String, body: String): String = suspendCoroutine { continuation ->

        val init = RequestInit(
            method = "POST",
            headers = newHeaders(),
            body = (body)
        )

        window.fetch(url, init).then { response ->
            if (response.ok)
                response.text().then { text ->
                    continuation.resume(text)
                } else
                response.text().then { text ->
                    continuation.resumeWithException(Exception("HTTP error ${response.status}:\n > $text"))
                }
        }.catch { e ->
            continuation.resumeWithException(e)
        }
    }
}