@file:OptIn(ExperimentalSerializationApi::class)

package classes.misc.network.models

import classes.misc.network.Fetcher
import kotlinx.serialization.*
import kotlinx.serialization.json.Json


val json = Json { encodeDefaults = true }




@Serializable
data class Message(val role :String, var content :String) {
    override fun toString(): String = "${(role+":").padEnd(11)} ${content}"
}



fun chatBody(
    temperature: Double = 0.6,
    presencePenalty: Double = 0.4,
    frequencyPenalty: Double = 0.2,
    block: ChatBody.() -> Unit
): ChatBody {
    val requestBody = ChatBody(temperature, presencePenalty, frequencyPenalty, "gpt-3.5-turbo")
    requestBody.block()
    return requestBody
}














@Serializable
data class ChatBody(

    var temperature: Double,
    @SerialName("presence_penalty")
    var presencePenalty: Double,
    @SerialName("frequency_penalty")
    var frequencyPenalty: Double,
    val model: String = "gpt-3.5-turbo",
    var messages: MutableList<Message> = mutableListOf(),
    @SerialName("max_tokens")
    val maxTokens: Int = 2600,
){

    fun ChatBody.user(text: String) =
        messages.add(Message("user", text))
    fun ChatBody.assistant(text: String) =
        messages.add(Message("assistant", text))
    fun ChatBody.system(text: String) =
        messages.add(Message("system", text))


    override fun toString(): String {
        val sb = StringBuilder()
        sb.append("ChatBody{\n")
        sb.append("  temperature:    $temperature\n")
        sb.append("  presence pen:   $presencePenalty\n")
        sb.append("  frequency pen:  $frequencyPenalty\n")
        sb.append("  messages:\n")
        for (message in messages)
            sb.append("    - $message\n")
        sb.append("}")
        return sb.toString()
    }


    suspend fun sendRequest() :String {
        val responseStr  = Fetcher.post("https://api.openai.com/v1/chat/completions", json.encodeToString(this))
        val choiceresult = (JSON.parse<String?>(responseStr).asDynamic()).choices[0].message.content
        // add assistant message to instance
        this.assistant(choiceresult)
        //console.log(choiceresult)
        return choiceresult;
    }



}



